<template>
  <section class="hot">
    <div class="hotSon">
      <div class="hot-top">
        <div class="hotimg"></div>
        <img
          class="hotbg"
          src="https://s3.music.126.net/mobile-new/img/hot_music_bg_3x.jpg?2bfefd3ba37eaffbd66b7d108b9de26a="
        />
        <div class="time">
          <span
            >更新日期：{{
              (new Date().getMonth() + 1).toString().padStart(2, "0")
            }}月{{ new Date().getDate().toString().padStart(2, "0") }}日</span
          >
        </div>
      </div>
      <SongList
        v-for="(item, index) in hotlist"
        :key="item.id"
        :item="item"
        :class="{ cl: index < 3 }"
        :songItemId="songItemId"
        :playstart="playstart"
        @get-song-list="
          $emit('get-song-list', $event);
          $emit('get-home-play', hotlist);
        "
        >{{ (index + 1).toString().padStart(2, "0") }}</SongList
      >
    </div>
