<script>
import SongList from "@/components/SongList.vue";
export default {
  components: { SongList },
  props: {
    songItemId: {
      type: Number,
    },
    playstart: {
      type: Boolean,
    },
  },
  data: function () {
    return {
      hotlist: [],
    };
  },
  computed: {
    // getHot: function () {
    //   console.log(this.hotlist.splic(0, 20));
    // },
  },
  created: function () {
    this.axios
      .get("http://apis.netstart.cn/music/playlist/detail?id=3778678")
      .then((res) => {
        this.hotlist = res.data.playlist.tracks.slice(0, 20);
      });
  },
};
</script>

